export function getImageUrls_color(category) {
  var urls = [];
  var dir = "/objects/color/" + category + "/";//used for local build
  //var dir = "http://henrik.eideberg.se/projects/machinelearning/my-image-object-recognition-machine/objects/color/" + category + "/";//used for online build
  var file = "";
  for(let i=1; i<10; i++){
    file = "image_000" + i + ".jpg";
    urls.push(dir+file);
  }
  for(let i=10; i<71; i++){
    file = "image_00" + i + ".jpg";
    urls.push(dir+file);
  }
  return urls;
}

