import * as tf from '@tensorflow/tfjs';

export class Model {
  constructor() {
    this.machine = null;
    this.history = null;
  }
  
  createAndCompileModel(inputSettings,
                        modelSettings,
                        convSettings,
                        nnSettings) {
    
    //console.log(inputSettings);
    //instantiate our Sequential model with tf.sequential: 
    this.machine = null;
    this.machine = tf.sequential();
    var radix = 10;
    var returnString = "";

    try {
    /* 
     * Add the configured two-dimensional convolutional layer(s).
     * For more information about convolutions see,
     * http://colah.github.io/posts/2014-07-Understanding-Convolutions/
    */    
    //console.log('convSettings.nrOfLayers.length', convSettings.nrOfLayers.length);
    for(let i=0; i<convSettings.nrOfLayers.length; i++){
      if(i === 0) {//First layer has a fixed input shape
        console.log('Adding first conv layer');
        //console.log('convSettings.filterSize[i]', convSettings.filterSize[i]);
        //console.log('parseInt(convSettings.filterSize[i])', parseInt(convSettings.filterSize[i]));
        //console.log('parseInt(convSettings.strides[i])', parseInt(convSettings.strides[i]));
        this.machine.add(tf.layers.conv2d({
          inputShape: [300, 300, 1],
          kernelSize: parseInt(convSettings.filterSize[i], radix),//5,
          filters: parseInt(convSettings.nrOfFilters[i], radix),//8,
          strides: parseInt(convSettings.strides[i], radix),//1,
          activation: convSettings.activationFunction[i],//'relu',
          kernelInitializer: convSettings.filterInitializer[i]//'varianceScaling'
        }));
        //console.log(convSettings.poolLayer[i]);
        if(convSettings.poolLayer[i] === '1'){
          console.log('Adding pool layer to first conv layer');
          //Add a max pooling layer only if configured/set in gui
          //console.log(convSettings.poolSize[i]);
          //console.log(parseInt(convSettings.poolSize[i]));
          //console.log(convSettings.poolStrides[i]);
          this.machine.add(tf.layers.maxPooling2d({
            poolSize: [parseInt(convSettings.poolSize[i], radix), parseInt(convSettings.poolSize[i], radix)],
            strides: [parseInt(convSettings.poolStrides[i], radix), parseInt(convSettings.poolStrides[i], radix)]
          }));
        }
      }
      else {//following layers will have 'auto' inputShape
        console.log('Adding convolutional layer nr: ', i+1);
        this.machine.add(tf.layers.conv2d({
          kernelSize: parseInt(convSettings.filterSize[i], radix),//5,
          filters: parseInt(convSettings.nrOfFilters[i], radix),//16,
          strides: parseInt(convSettings.strides[i], radix),//1,
          activation: convSettings.activationFunction[i],//'relu',
          kernelInitializer: convSettings.filterInitializer[i]//'varianceScaling'
        }));
        if(convSettings.poolLayer[i] === '1'){
          console.log('Adding pool layer to conv layer nr ', i+1);
          this.machine.add(tf.layers.maxPooling2d({
            poolSize: [parseInt(convSettings.poolSize[i], radix), parseInt(convSettings.poolSize[i], radix)],
            strides: [parseInt(convSettings.poolStrides[i], radix), parseInt(convSettings.poolStrides[i], radix)]
          }));
        }
      }
    }
    
    //Add a flatten layer to flatten the output from the convolutional layers
    console.log('Add flatten layer');
    this.machine.add(tf.layers.flatten());
    
    //Add additional neural layers if configured by user
    //console.log('nnSettings.nrOfLayers.length', nnSettings.nrOfLayers.length);
    for(let j=0; j<nnSettings.nrOfLayers.length; j++){
      console.log('Adding neural layer nr ', j+1);
      //console.log('nnSettings.activationFunction', nnSettings.activationFunction[j]);
      this.machine.add(tf.layers.dense({
        units: parseInt(nnSettings.nrOfNeurons[j], radix),
        kernelInitializer: nnSettings.filterInitializer[j],//'varianceScaling',
        activation: nnSettings.activationFunction[j]//'softmax'
      }));
    }
    
    /*
     * Add final neural layer with output/neurons/units set to number of selected categories.
     * kernelInitializer and activation function are hardcoded in this layer.
     * TBD - this shall be mentioned in the GUI!!
    */
    //console.log(inputSettings.categories.size);
    console.log('Adding final dense layer with ', inputSettings.categories.size,' neurons');
    this.machine.add(tf.layers.dense({
      units: inputSettings.categories.size,
      kernelInitializer: 'varianceScaling',
      activation: 'softmax'
    }));

    //Set the training characterisitics.
    console.log('Set the training characterisitics');
    var optimizer = tf.train.sgd(parseFloat(modelSettings.learningRate));
    this.machine.compile({
      optimizer: optimizer,
      loss: 'categoricalCrossentropy',
      metrics: ['accuracy'],
    });
    
    //console.log(this.machine);
    returnString = 'OK';
    console.log(this.machine);
    }
    catch(err){
      returnString = err;
    }
    finally{
      console.log(returnString);
      return returnString;
    }
    //For debugging
    //console.log(this.machine);
    //console.log(this.machine.layers);
    //console.log('Model created succesfully');
  }

  async train() {
    //LOOP
    // - get a training batch of size batchSize
    //    - start with (batchsize 1 = 1 image)
    //    - when batchSeize 1 works, move to batchSize > 1
    // - train the model with that batch
    //this.history = this.machine.fit();
    // - get the history
    //    history.loss
    //    history.accuracy
    //    PLOT?
    // - dispose the batch
    
    //does this work in react? https://blog.cloudboost.io/using-async-await-with-react-b807f9d7b64a
    await tf.nextFrame();
    //tf.nextFrame().then(console.log('001'));
    //console.log('002');
  }
}


